const generateProducts = (num) => {
    const names = [
        "Laptop", "Smartphone", "Tai nghe", "Bàn phím", "Chuột", "Màn hình", "Máy tính bảng", "Loa Bluetooth",
        "Đồng hồ thông minh", "Sạc dự phòng", "Camera IP", "Router Wi-Fi", "Ổ cứng SSD", "Máy in", "Tay cầm chơi game",
        "Micro thu âm", "Webcam", "Tản nhiệt", "Nguồn máy tính", "Ghế Gaming"
    ];

    const adjectives = [
        "Pro Max", "Ultra Gaming", "Thế hệ 12", "Chống nước IP68", "Không dây", "Siêu mỏng nhẹ", "Cơ học RGB",
        "Chống ồn chủ động", "Màn hình OLED", "Pin khủng 5000mAh", "Nhôm nguyên khối", "Kết nối đa điểm",
        "Độ phân giải 4K", "Tốc độ cao", "Phiên bản giới hạn", "Thiết kế Ergonomic", "Sạc nhanh 65W",
        "Âm thanh Hi-Res", "Bảo hành 2 năm", "Hỗ trợ AI"
    ];

    return Array.from({ length: num }, (_, i) => {
        const randomName = names[Math.floor(Math.random() * names.length)];
        const randomAdj = adjectives[Math.floor(Math.random() * adjectives.length)];
        const productName = `${randomName} ${randomAdj}`;

        // Tạo mô tả ít nhất 30 từ
        const baseDesc = `Sản phẩm ${productName} là sự lựa chọn hoàn hảo cho nhu cầu công việc và giải trí hàng ngày của bạn. Với công nghệ tiên tiến nhất được tích hợp, chúng tôi cam kết mang lại hiệu năng vượt trội, độ bền bỉ cao theo thời gian và trải nghiệm người dùng tối ưu nhất trong phân khúc giá này. Bạn hoàn toàn có thể yên tâm về chất lượng sản phẩm đã được kiểm định nghiêm ngặt trước khi đến tay khách hàng.`;
        
        return {
            id: i + 1,
            name: productName,
            description: baseDesc, // Nội dung trên đã đủ > 35 từ
            sold: Math.floor(Math.random() * 10000),
            price: Math.floor(Math.random() * (50000000 - 100000 + 1)) + 100000
        };
    });
};


